body {
  min-width: 320px;
}

*, *::before, *::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.content-box-component {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}

a {
  text-decoration: none;

  transition: all 0.2s ease;

  &:active {
    opacity: 0.7;
  }
}

img {
  max-width: 100%;
  height: auto;
}
