body {
  min-width: 320px;
}
*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.content-box-component {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}
a {
  text-decoration: none;
  transition: all 0.2s ease;
}
a:active {
  opacity: 0.7;
}
img {
  max-width: 100%;
  height: auto;
}
.screen-reader-text,
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 1230px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (max-width: 1230px) {
  .container {
    width: 100%;
    padding: 0 20px;
  }
}
@media (max-width: 763px) {
  .container {
    padding: 0 10px;
  }
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
/* Slider */
.slick-loading .slick-list {
  background: #fff url('../img/ajax-loader.gif') center center no-repeat;
}
/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  z-index: 10;
  top: 50%;
  display: block;
  padding: 0;
  margin-top: -12px;
  height: 46px;
  width: 69px;
  line-height: 0;
  font-size: 0;
  color: transparent;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  border: none;
  outline: none;
  cursor: pointer;
  opacity: 0.8;
  transition: all 0.1s ease;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url("../img/slider-arrow.png");
}
.slick-prev:hover,
.slick-next:hover,
.slick-prev:focus,
.slick-next:focus {
  opacity: 1;
}
.slick-prev {
  left: -50px;
}
.slick-next {
  right: -50px;
  margin-top: -34px;
  transform: rotate(180deg);
}
/********************/
/*       Dots       */
/********************/
.slick-dotted .slick-slider {
  margin-bottom: 30px;
}
.slick-dots {
  position: absolute;
  bottom: -70px;
  display: block;
  padding: 0;
  margin: 0;
  width: 100%;
  text-align: center;
  list-style: none;
}
.slick-dots li {
  position: relative;
  display: inline-block;
  height: 30px;
  width: 30px;
  margin: 0 10px;
  padding: 0;
  cursor: pointer;
}
.slick-dots li button {
  position: relative;
  display: block;
  height: 30px;
  width: 30px;
  padding: 5px;
  line-height: 0;
  font-size: 0;
  color: transparent;
  outline: none;
  cursor: pointer;
  border: 1px solid #8ad0ef;
  background-color: #ffffff;
  border-radius: 50%;
  opacity: 1;
}
.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 0.8;
}
.slick-dots li button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -10px;
  margin-top: -10px;
  width: 20px;
  height: 20px;
  font-size: 0;
  line-height: 20px;
  text-align: center;
  color: transparent;
  background-color: #e6f1f8;
  border-radius: 50%;
}
.slick-dots li.slick-active button {
  border-color: #f93648;
}
.slick-dots li.slick-active button:before {
  background-color: #fbce5e;
}
.slick-slide {
  outline: none;
}
.rooms__slider .slick-prev,
.rooms__slider .slick-next {
  background-image: url("../img/slider-arrow-dark.png");
}
@media (max-width: 1230px) {
  .slick-prev {
    left: 0;
  }
  .slick-next {
    right: 0;
  }
}
@media (max-width: 763px) {
  .slick-prev {
    left: 0;
  }
  .slick-next {
    right: 0;
  }
}
body.lb-disable-scrolling {
  overflow: hidden;
}
.lightboxOverlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: black;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
  opacity: 0.8;
  display: none;
}
.lightbox {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 10000;
  text-align: center;
  line-height: 0;
  font-weight: normal;
  outline: none;
}
.lightbox .lb-image {
  display: block;
  height: auto;
  max-width: inherit;
  max-height: none;
  border-radius: 3px;
  /* Image border */
  border: 4px solid white;
}
.lightbox a img {
  border: none;
}
.lb-outerContainer {
  position: relative;
  *zoom: 1;
  width: 250px;
  height: 250px;
  margin: 0 auto;
  border-radius: 4px;
  /* Background color behind image.
     This is visible during transitions. */
  background-color: white;
}
.lb-outerContainer:after {
  content: "";
  display: table;
  clear: both;
}
.lb-loader {
  position: absolute;
  top: 43%;
  left: 0;
  height: 25%;
  width: 100%;
  text-align: center;
  line-height: 0;
}
.lb-cancel {
  display: block;
  width: 32px;
  height: 32px;
  margin: 0 auto;
  background: url(../img/loading.gif) no-repeat;
}
.lb-nav {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
}
.lb-container > .nav {
  left: 0;
}
.lb-nav a {
  outline: none;
  background-image: url('data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==');
}
.lb-prev,
.lb-next {
  height: 100%;
  cursor: pointer;
  display: block;
}
.lb-nav a.lb-prev {
  width: 34%;
  left: 0;
  float: left;
  background: url(../img/prev.png) left 48% no-repeat;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  -moz-transition: opacity 0.6s;
  -o-transition: opacity 0.6s;
  transition: opacity 0.6s;
}
.lb-nav a.lb-prev:hover {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}
.lb-nav a.lb-next {
  width: 64%;
  right: 0;
  float: right;
  background: url(../img/next.png) right 48% no-repeat;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  -moz-transition: opacity 0.6s;
  -o-transition: opacity 0.6s;
  transition: opacity 0.6s;
}
.lb-nav a.lb-next:hover {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}
.lb-dataContainer {
  margin: 0 auto;
  padding-top: 5px;
  *zoom: 1;
  width: 100%;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.lb-dataContainer:after {
  content: "";
  display: table;
  clear: both;
}
.lb-data {
  padding: 0 4px;
  color: #ccc;
}
.lb-data .lb-details {
  width: 85%;
  float: left;
  text-align: left;
  line-height: 1.1em;
}
.lb-data .lb-caption {
  font-size: 13px;
  font-weight: bold;
  line-height: 1em;
}
.lb-data .lb-caption a {
  color: #4ae;
}
.lb-data .lb-number {
  display: block;
  clear: left;
  padding-bottom: 1em;
  font-size: 12px;
  color: #999999;
}
.lb-data .lb-close {
  display: block;
  float: right;
  width: 30px;
  height: 30px;
  background: url(../img/close.png) top right no-repeat;
  text-align: right;
  outline: none;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
  -webkit-transition: opacity 0.2s;
  -moz-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.lb-data .lb-close:hover {
  cursor: pointer;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}
.title {
  font-family: "Patrick Hand", sans-serif;
  font-size: 56px;
  line-height: 60px;
  font-weight: 400;
  text-align: center;
  text-shadow: 1px 1px 1px #03405c;
}
.title--green {
  color: #2dcc4a;
}
.title--blue {
  color: #03405c;
}
.title--white {
  color: #ffffff;
}
.title--lightblue {
  color: #14a0db;
}
@media (max-width: 763px) {
  .title {
    font-size: 34px;
    line-height: 42px;
    letter-spacing: -0.02em;
  }
}
.button {
  width: 249px;
  height: 84px;
  padding-left: 22px;
  padding-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Patrick Hand", sans-serif;
  font-size: 28px;
  line-height: 34px;
  color: #03405c;
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../img/button-bg.png");
  border: none;
  outline: none;
  cursor: pointer;
  transition: all 0.2s ease;
}
.button:hover,
.button:focus {
  opacity: 0.85;
}
.button:active {
  opacity: 0.7;
}
.page {
  padding-top: 100px;
}
.page__subtitle {
  position: relative;
  margin: 0;
  padding-top: 53px;
  margin-bottom: 30px;
  font-size: 32px;
  line-height: 42px;
  color: #03405c;
  letter-spacing: -0.008em;
}
.page__subtitle::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 268px;
  height: 28px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url("../img/wave.png");
}
@media (max-width: 763px) {
  .page {
    padding-top: 70px;
  }
  .page__subtitle {
    font-size: 22px;
    line-height: 32px;
  }
  .page__subtitle::before {
    top: 6px;
  }
}
.burger {
  padding: 0;
  width: 42px;
  height: 37px;
  border: none;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../img/burger.png");
  background-color: transparent;
  cursor: pointer;
  outline: none;
}
.burger--open {
  background-image: url("../img/menu-close.png");
}
.burger:hover,
.burger:focus {
  opacity: 0.8;
}
.burger:active {
  opacity: 0.5;
}
.posts__pagination {
  margin-top: 45px;
}
.nav-links {
  display: flex;
}
.pagination {
  display: flex;
  justify-content: center;
}
.page-numbers {
  min-width: 30px;
  min-height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  font-size: 24px;
  line-height: 22px;
  font-weight: 700;
  color: #03405c;
  text-align: center;
}
.page-numbers:hover,
.page-numbers:focus {
  color: #14a0db;
}
.current {
  color: #ffffff;
  background-color: #14a0db;
  border: 1px solid #14a0db;
  border-radius: 5px;
}
.prev,
.next {
  border: none;
}
.first {
  position: relative;
  padding-bottom: 44px;
  background-color: #e6f1f8;
}
.first::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 60%;
  background-repeat: no-repeat;
  background-position: center left;
  background-size: cover;
  background-image: url("../img/first.png");
}
.first--inner {
  padding: 0;
}
.first--inner::before {
  display: none;
}
@media (max-width: 1230px) {
  .first {
    overflow: hidden;
    padding-bottom: 62px;
  }
  .first--visible {
    overflow: visible;
  }
  .first--inner {
    padding-bottom: 30px;
